<template>
    <div id="commemorations">
        <v-card class="commemoration" v-for="item in items" :key="item.id">
            <v-card class="col-auto">
                <v-img v-if="item.backgroundUrl.length!==0"
                       :src="item.backgroundUrl"
                       class="white--text align-end"
                       gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                       max-height="500px">
                    <v-card-actions>
                        <v-card-title v-text="item.days"/>
                        <v-spacer></v-spacer>
                        <v-btn v-show="item.remark.length!==0" icon @click="item.show = !item.show">
                            <v-icon color="white" >{{ item.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                        </v-btn>
                    </v-card-actions>
                </v-img>
                <v-card-actions v-else>
                    <v-card-title v-text="item.days"/>
                    <v-spacer></v-spacer>
                    <v-btn v-show="item.remark.length!==0" icon @click="item.show = !item.show">
                        <v-icon>{{ item.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                    </v-btn>
                </v-card-actions>
                <v-expand-transition>
                    <div v-show="item.show">
                        <v-divider></v-divider>
                        <v-card-text v-text="item.remark"/>
                    </div>
                </v-expand-transition>
            </v-card>
        </v-card>
    </div>
</template>

<script>
    import axios from 'axios';
    import util from '../util/util'

    export default {
        name: "commemoration",
        data: function () {
            return {
                items: []
            }
        },
        mounted() {
            this.getCommemorations()
        },
        methods: {
            getCommemorations() {
                let vm = this;
                axios.post(util.COMMEMORATION_QUERY_ALL)
                    .then(function (response) {
                        let result = response.data;
                        if (result.error === 1) {
                            let list = JSON.parse(result.body);
                            list = list.sort(function (a, b) {
                                return a.startTimeMills - b.startTimeMills
                            });
                            let milliseconds = new Date().getTime();
                            for (let i = 0; i < list.length; i++) {
                                let days = util.diffTime(list[i].startTimeMills, milliseconds).days;
                                list[i].show = false;
                                if (milliseconds - list[i].startTimeMills > 0) {
                                    list[i].days = '第' + (days + 1) + '天'
                                } else {
                                    list[i].days = '还有' + (-(days - 1)) + '天'
                                }
                                list[i].days = (list[i].title + " " + list[i].days)
                            }
                            vm.items = list
                        }
                    }).catch(function (error) {
                    console.log(error)
                });
            }
        }
    }
</script>

<style scoped>
    .commemoration {
        margin: 10px;
    }
</style>